<template>
    <div class="paycpm">
        <div class="pay_list">
            <div class="pay_type pay_alipay" @click="changePay('alipay')">
                <span class="check" v-if="checkPaytype === 'alipay'">
                    <i class="el-icon-check"></i>
                </span>
                支付宝
            </div>
            <div class="pay_type pay_wechat" @click="changePay('wechat')">
                <span class="check" v-if="checkPaytype === 'wechat'">
                    <i class="el-icon-check"></i>
                </span>
                微信
            </div>
        </div>
        <div class="pay_title">支付金额：{{ pay_money | changeMoney }}</div>
        <div class="pay_qrcode">
            <el-image :src="require('./qrcode.png')">
                <div slot="placeholder" class="image-slot">
                    加载中<span class="dot">...</span>
                </div>
            </el-image>
        </div>
    </div>
</template>
<script>
    export default {
        props: {
            pay_money: Number
        },
        data() {
          return {
              checkPaytype: 'alipay'
          }
        },
        methods: {
          changePay(val) {
              this.checkPaytype = val
          }
        },
        filters: {
            changeMoney(val) {
                return val.toFixed(2)
            }
        }
    }
</script>
<style lang="scss" scoped>
    .paycpm {
        padding: 15px 0;
        .pay_list {
            display: flex;
            justify-content: space-around;

            .pay_type {
                width: 150px;
                height: 100px;
                line-height: 100px;
                font-size: 18px;
                text-align: center;
                color: #fff;
                border-radius: 4px;
                position: relative;
                overflow: hidden;
                .check {
                    position: absolute;
                    left: 0;
                    top: 0;
                    background: red;
                    width: 25px;
                    height: 25px;
                    line-height: 25px;
                }
            }

            .pay_alipay {
                background: #409EFF;
            }

            .pay_wechat {
                background: forestgreen;
            }
        }
        .pay_title {
            text-align: center;
            padding: 20px 0;
            font-size: 18px;
        }
        .pay_qrcode {
            width: 100px;
            height: 100px;
            margin: 0 auto;
        }
    }
</style>