<style lang="scss">@import "./css/style.scss";</style>
<template>
    <div id="index">
        <ToolBar>
            <div>
                <el-button type="primary" icon="el-icon-plus" size="small" @click="sendOrder">发布订单</el-button>
            </div>
            <div>
                <el-input
                    placeholder="请输入联系人电话或姓名"
                    size="small"
                    style="width: 200px"
                    v-model="searchParams.postTitle"
                    clearable>
                </el-input>
                <el-select v-model="searchParams.postType" size="small" clearable placeholder="服务类型"
                           style="width: 140px">
                    <el-option
                        v-for="(v,k) in $Cfg.postType"
                        :key="k"
                        :label="v"
                        :value="k">
                    </el-option>
                </el-select>

                <el-select v-model="searchParams.postStatus" size="small" clearable placeholder="服务规格"
                           style="width: 120px">
                    <el-option
                        v-for="(v,k) in $Cfg.postStatus"
                        :key="k"
                        :label="v"
                        :value="k">
                    </el-option>
                </el-select>
                <el-select v-model="searchParams.postStatus" size="small" clearable placeholder="订单状态"
                           style="width: 120px">
                    <el-option
                        v-for="(v,k) in $Cfg.postStatus"
                        :key="k"
                        :label="v"
                        :value="k">
                    </el-option>
                </el-select>
                <el-button type="success" size="small" @click="refresh = !refresh">查询</el-button>
                <el-button type="warning" size="small" @click="refresh = !refresh">重置</el-button>
            </div>
        </ToolBar>
        <el-table
            :data="tableData"
            border
            style="width: 100%">
            <el-table-column
                prop="order"
                label="服务订单编号"
                width="140">
            </el-table-column>
            <el-table-column
                prop="name"
                label="服务对象"
                width="90">
            </el-table-column>
            <el-table-column
                prop="name"
                label="联系人"
                width="90">
            </el-table-column>
            <el-table-column
                prop="phone"
                label="联系电话"
                width="120">
                18924171171
            </el-table-column>
            <el-table-column
                prop="name"
                label="所在城市"
                width="80">
            </el-table-column>
            <el-table-column
                prop="name"
                label="服务产品">
            </el-table-column>
            <el-table-column
                prop="name"
                label="服务规格">
            </el-table-column>
            <el-table-column
                prop="name"
                label="购买数量"
                width="90">
            </el-table-column>
            <el-table-column
                prop="name"
                label="总服务金额"
                width="100">
            </el-table-column>
            <el-table-column
                prop="time"
                label="服务开始时间"
                width="130">
            </el-table-column>
            <el-table-column
                prop="time"
                label="服务结束时间"
                width="130">
            </el-table-column>
            <el-table-column
                prop="time"
                label="订单创建时间"
                width="130">
            </el-table-column>
            <el-table-column
                prop="type"
                label="订单状态"
                width="80">
            </el-table-column>
            <el-table-column
                prop="type"
                label="接单类型"
                width="80">
            </el-table-column>

            <el-table-column
                label="操作"
                width="158">
                <template slot-scope="scope">
                    <el-button @click="payOrder(scope.row)" type="success" size="small"
                    >去支付
                    </el-button>
                    <el-button @click="previewOrder(scope.row)" type="info" size="small"
                    >查看
                    </el-button>
                </template>
            </el-table-column>
        </el-table>
        <Pagination
            :params="searchParams"
            :requestFunc="requestFunc"
            @returnData="returnData"
        />
    </div>
</template>

<script>
    import ToolBar from '@/components/ToolBar.vue';
    import HelpHint from '@/components/HelpHint.vue';
    import Pagination from '@/components/Pagination.vue';
    import paginationMixin from './mixin/page.js'
    import PayDialog from '../common/PayDialog'

    export default {
        mixins: [paginationMixin],
        data() {
            return {
                tableData: [{
                    order: '000000001',
                    date: '2016-05-03',
                    time: '2019-4-11 14:50',
                    type: '已接单',
                    name: 'Lorem ipsum dolor sit amet,',
                    address: 'Lorem ipsum dolor sit amet, consectetur adipisicing elit. Asperiores fugit in quae vero. Adipisci blanditiis dignissimos eum facere laudantium quasi ratione repellat vitae! Alias consequatur dolores enim neque similique unde.'
                }, {
                    order: '000000001',
                    date: '2016-05-02',
                    time: '2019-4-11 14:50',
                    type: '服务完成',
                    name: 'Lorem ipsum dolor sit amet,',
                    address: 'Lorem ipsum dolor sit amet, consectetur adipisicing elit. Asperiores fugit in quae vero. Adipisci blanditiis dignissimos eum facere laudantium quasi ratione repellat vitae! Alias consequatur dolores enim neque similique unde.'
                }]
            }
        },
        methods: {
            goPayOrder() {
                this.$router.push('/pay_order_3')
            },
            payOrder(row) {
                const h = this.$createElement;
                this.$msgbox({
                    title: '支付方式',
                    message: h('PayDialog', {props: {pay_money: 3000}},),
                    showCancelButton: false,
                    showConfirmButton: false,
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    beforeClose: (action, instance, done) => {
                        if (action === 'confirm') {
                            instance.confirmButtonLoading = true;
                            instance.confirmButtonText = '执行中...';
                            setTimeout(() => {
                                done();
                                setTimeout(() => {
                                    instance.confirmButtonLoading = false;
                                }, 300);
                            }, 3000);
                        } else {
                            done();
                        }
                    }
                }).then(action => {
                    this.$message({
                        type: 'info',
                        message: 'action: ' + action
                    });
                });
            },
            previewOrder(row) {
                this.$router.push('/preview_order_3')
            },
            sendOrder() {
                this.$router.push('/send_order_3')
            }
        },
        created() {

        },
        components: {
            ToolBar, HelpHint, Pagination, PayDialog
        }
    }
</script>
